import React, { useState, useContext } from "react";
import { motion, AnimatePresence } from "framer-motion";
import "./css/Matreshka.css";
import SoundContext from './SoundContext';

export const promoList = [
  { label: "  -30%  ", code: "PROMO30" },
  { label: "  RuTube", code: "RUTUBE1M" },
  { label: " -10%  ", code: "PROMO10" },
  { label: " -40%   ", code: "PROMO40" },
  { label: " -20%  ", code: "PROMO20" },
  { label: "  RuTube", code: "RUTUBE3M" },
  { label: " -60%  ", code: "PROMO60" },
  { label: " -70%  ", code: "PROMO70" },
  { label: " -80%  ", code: "PROMO80" },
  { label: " -15%  ", code: "PROMO15" },
  { label: " -25%  ", code: "PROMO25" },
];

const PromoNotification = ({ message }) => {
  return (
    <AnimatePresence>
      {message && (
        <motion.div
          className="promo-notification"
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          exit={{ opacity: 0, y: 20 }}
          transition={{ duration: 0.3 }}
        >
          {message}
        </motion.div>
      )}
    </AnimatePresence>
  );
};

function PromoCodeTicket({ promoCode }) {
  const [notification, setNotification] = useState(null);
  const { playButtonClickSound } = useContext(SoundContext);

  const copyToClipboard = () => {
    playButtonClickSound();
    navigator.clipboard.writeText(promoCode.code).then(() => {
      setNotification(" !");
      setTimeout(() => setNotification(null), 3000);
    }).catch(err => {
      console.error('Failed to copy: ', err);
    });
  };

  return (
    <motion.div
      className="promo-ticket-container"
      initial={{ opacity: 0, y: 50 }}
      animate={{ opacity: 1, y: 0 }}
      exit={{ opacity: 0, y: 50 }}
      transition={{ duration: 0.5 }}
    >
      <div className="promo-ticket bg-white p-4 rounded-lg shadow-md">
        <motion.p className="font-bold text-lg">
          {promoCode ? promoCode.label : ""}
        </motion.p>
        <motion.p className="text-gray-700 mt-2">
           : <strong>{promoCode ? promoCode.code : ""}</strong>
        </motion.p>
        <motion.button
          onClick={copyToClipboard}
          className="copy-button"
          whileHover={{ scale: 1.05 }}
          whileTap={{ scale: 0.95 }}
        >
           
        </motion.button>
      </div>
      {notification && <PromoNotification message={notification} />}
    </motion.div>
  );
}

export default PromoCodeTicket;